@charset "UTF-8";
@import './vendors/choices.min.css';
@import './vendors/jquery-ui.min.css';
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden], template {
  display: none;
}

html {
  background: #fff;
  color: #000;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

html,
button,
input,
select,
textarea {
  font-family: sans-serif;
}

body {
  margin: 0;
}

a {
  background: transparent;
}
a:focus {
  outline: none;
}
a:hover, a:active {
  outline: 0;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

h2 {
  font-size: 1.5em;
  margin: 0.83em 0;
}

h3 {
  font-size: 1.17em;
  margin: 1em 0;
}

h4 {
  font-size: 1em;
  margin: 1.33em 0;
}

h5 {
  font-size: 0.83em;
  margin: 1.67em 0;
}

h6 {
  font-size: 0.75em;
  margin: 2.33em 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

dfn {
  font-style: italic;
}

mark {
  background: #ff0;
  color: #000;
}

code,
kbd,
pre,
samp {
  font-family: monospace, serif;
  font-size: 1em;
}

pre {
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word;
}

q {
  quotes: "" "" "" "";
}

q:before, q:after {
  content: "";
  content: none;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

figure {
  margin: 0;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  border: 0;
  padding: 0;
  white-space: normal;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
}

button, input {
  line-height: normal;
}

button,
select {
  text-transform: none;
}

button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
input[disabled] {
  cursor: default;
}

button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}
input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}
input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

@font-face {
  font-family: "Muller";
  src: url("../fonts/mullerbold-webfont.woff2") format("woff2"), url("../fonts/mullerbold-webfont.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Muller";
  src: url("../fonts/mullermedium-webfont.woff2") format("woff2"), url("../fonts/mullermedium-webfont.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Muller";
  src: url("../fonts/mullerregular-webfont.woff2") format("woff2"), url("../fonts/mullerregular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Muller";
  src: url("../fonts/mullerregularitalic-webfont.woff2") format("woff2"), url("../fonts/mullerregularitalic-webfont.woff") format("woff");
  font-weight: 400;
  font-style: italic;
}
@font-face {
  font-family: "Muller";
  src: url("../fonts/mullerthinitalic-webfont.woff2") format("woff2"), url("../fonts/mullerthinitalic-webfont.woff") format("woff");
  font-weight: 300;
  font-style: italic;
}
@font-face {
  font-family: "Muller";
  src: url("../fonts/mullerlightitalic-webfont.woff2") format("woff2"), url("../fonts/mullerlightitalic-webfont.woff") format("woff");
  font-weight: 300;
  font-style: italic;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-regular-webfont.woff2") format("woff2"), url("../fonts/opensans-regular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
* {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  font-family: "Muller", "Open Sans", sans-serif;
  background-color: #fff;
  scroll-behavior: smooth;
  height: 100%;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: #000;
  display: inline-flex;
}

h2, h3 {
  overflow-wrap: anywhere;
}

img {
  display: inline-block;
}

.container {
  margin-left: auto;
  margin-right: auto;
  max-width: 1110px;
  width: 100%;
}
@media (max-width: 1350px) {
  .container {
    max-width: 824px;
  }
}
@media (max-width: 1020px) {
  .container {
    max-width: 540px;
  }
}
@media (max-width: 766px) {
  .container {
    max-width: 80%;
  }
}

.hidden {
  display: none !important;
}

.no-scroll {
  overflow-y: hidden;
}

.overlay {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  width: 100%;
  height: 100vh;
  background: rgba(18, 23, 35, 0.5);
  z-index: 100;
  transition: transform 0.3s ease-in-out;
  transform: translateY(-200vh);
}

.overlay_shown {
  transform: translateY(0);
}

.modal * {
  font-family: "Muller", "Open Sans", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
}

.modal {
  position: relative;
  min-height: 655px;
  max-width: 824px;
  width: 100%;
  padding: 60px;
  background-color: #121723;
}
@media (max-width: 1020px) {
  .modal {
    max-width: 540px;
    padding: 60px 40px;
  }
}
@media (max-width: 766px) {
  .modal {
    max-width: 80%;
    min-height: 442px;
    padding: 40px 20px;
  }
}
.modal__close {
  position: absolute;
  top: 30px;
  right: 30px;
  width: 42px;
  height: 42px;
}
@media (max-width: 766px) {
  .modal__close {
    top: 20px;
    right: 20px;
    width: 28px;
    height: 28px;
  }
}
@media (max-width: 766px) {
  .modal__close {
    top: 14px;
    right: 20px;
    width: 28px;
    height: 28px;
  }
}
.modal__close circle, .modal__close path {
  transition: stroke 0.3s ease-in-out;
}
.modal__close:hover circle, .modal__close:hover path {
  stroke: #6D31EE;
}
.modal__close:focus circle, .modal__close:focus path {
  stroke: #AA9DFA;
}
.modal__close:active circle, .modal__close:active path {
  stroke: #4F28A5;
}
.modal__header {
  margin-bottom: 17px;
  font-weight: 400;
  font-size: 48px;
  line-height: 48px;
  color: #fff;
}
@media (max-width: 766px) {
  .modal__header {
    margin-bottom: 8px;
    font-size: 24px;
    line-height: 24px;
  }
}
.modal__disclaimer {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-bottom: 30px;
  width: 100%;
}
@media (max-width: 766px) {
  .modal__disclaimer {
    display: flex;
    flex-direction: column;
    margin-bottom: 28px;
  }
}
.modal__disclaimer-text {
  display: inline;
  margin-right: 3px;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  color: #fff;
}
@media (max-width: 766px) {
  .modal__disclaimer-text {
    font-size: 12px;
    line-height: 24px;
  }
}
.modal__disclaimer-link {
  display: inline;
  cursor: pointer;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  color: #6D31EE;
  text-underline-offset: 4px;
  text-decoration: underline solid transparent;
  transition: color 0.3s ease-in-out, text-decoration-color 0.3s ease-in-out, background-color 0.3s ease-in-out;
}
@media (max-width: 766px) {
  .modal__disclaimer-link {
    font-size: 12px;
    line-height: 4px;
  }
}
.modal__disclaimer-link:hover {
  text-decoration-color: #6D31EE;
}
.modal__disclaimer-link:focus {
  outline: none;
  text-decoration-color: transparent;
  background-color: #AA9DFA;
  color: #fff;
}
.modal__disclaimer-link:active {
  outline: none;
  text-decoration-color: #4F28A5;
  background-color: transparent;
  color: #4F28A5;
}
.modal__form {
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-bottom: 30px;
}
@media (max-width: 766px) {
  .modal__form {
    gap: 20px;
    margin-bottom: 28px;
  }
}
.modal__form-fieldset {
  position: relative;
  margin: 0;
  max-width: none;
  width: 100%;
  padding: 0;
  border: none;
}
.modal__form-login-input, .modal__form-password-input {
  max-width: 526px;
  width: 100%;
  height: 70px;
  padding: 25px 32px;
  border: 1px solid #fff;
  border-radius: 40px;
  background-color: transparent;
  color: #fff;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
}
@media (max-width: 1020px) {
  .modal__form-login-input, .modal__form-password-input {
    max-width: none;
  }
}
@media (max-width: 766px) {
  .modal__form-login-input, .modal__form-password-input {
    height: 40px;
    padding: 12px 18px;
    font-size: 16px;
    line-height: 16px;
  }
}
.modal .just-validate-error-label {
  position: absolute;
  font-family: "Muller";
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  top: 10px;
  left: 34px;
}
@media (max-width: 766px) {
  .modal .just-validate-error-label {
    top: 2px;
    left: 17px;
  }
}
.modal .input-error {
  border: 1px solid #D52B1E;
}
.modal__form-login-input::placeholder, .modal__form-password-input::placeholder {
  color: #A1A6B4;
  font-family: "Muller";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
}
@media (max-width: 766px) {
  .modal__form-login-input::placeholder, .modal__form-password-input::placeholder {
    font-size: 16px;
    line-height: 16px;
  }
}
.modal__form-button {
  max-width: 167px;
  padding: 24.5px 53.5px;
  background: #6D31EE;
  border: 1px solid #6D31EE;
  border-radius: 40px;
  color: #fff;
  font-family: "Muller";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
}
@media (max-width: 766px) {
  .modal__form-button {
    max-width: 160px;
    padding: 11px 55.5px;
    font-size: 16px;
    line-height: 16px;
  }
}
.modal__form-button:hover {
  border-color: #AA9DFA;
  background-color: #AA9DFA;
}
.modal__form-button:focus {
  outline: none;
  border-color: #4F28A5;
  background-color: #AA9DFA;
}
.modal__form-button:active {
  outline: none;
  border-color: #4F28A5;
  background-color: #4F28A5;
}
.modal__bottom {
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding-top: 30px;
  border-top: 1px solid rgba(230, 232, 236, 0.4);
}
@media (max-width: 766px) {
  .modal__bottom {
    padding-top: 10px;
    gap: 10px;
  }
}
.modal__bottom-text {
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  color: #fff;
}
@media (max-width: 766px) {
  .modal__bottom-text {
    font-size: 12px;
    line-height: 24px;
    color: #A1A6B4;
  }
}
.modal__bottom-links-list {
  display: flex;
  flex-direction: row;
  gap: 40px;
}
@media (max-width: 766px) {
  .modal__bottom-links-list {
    gap: 48px;
  }
}
.modal__bottom-link svg rect {
  transition: fill 0.3s ease-in-out;
}
.modal__bottom-link:hover svg rect {
  fill: #6D31EE;
}
.modal__bottom-link:focus svg rect {
  fill: #AA9DFA;
}
.modal__bottom-link:active svg rect {
  fill: #4F28A5;
}
.modal__bottom-link_fb svg path {
  transition: fill 0.3s ease-in-out;
}
.modal__bottom-link_fb:hover svg path {
  fill: #6D31EE;
}
.modal__bottom-link_fb:focus svg path {
  fill: #AA9DFA;
}
.modal__bottom-link_fb:active svg path {
  fill: #4F28A5;
}
.modal .form__text {
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
}
.modal .form__text:hover {
  outline: none;
  background-color: #292E39;
}
.modal .form__text:focus {
  outline: none;
  background-color: #292E39;
  border-color: #AA9DFA;
}
.modal .form__text:active {
  outline: none;
  background-color: transparent;
  border-color: #fff;
}

.header {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.header__nav-top {
  display: flex;
  align-items: center;
  padding: 20px 0;
  border-bottom: 1px solid #E6E8EC;
}
@media (max-width: 1350px) {
  .header__nav-top {
    position: relative;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0;
  }
}
.header__nav-top::after {
  display: none;
  content: "";
}
@media (max-width: 1350px) {
  .header__nav-top::after {
    display: inline-flex;
    position: absolute;
    width: 100%;
    height: 60px;
    top: 0;
    left: 0;
    background-color: #E6E8EC;
  }
}
@media (max-width: 766px) {
  .header__nav-top::after {
    height: 54px;
  }
}
.header__nav-top-content {
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 100%;
}
@media (max-width: 1350px) {
  .header__nav-top-content {
    flex-wrap: wrap;
  }
}
@media (max-width: 766px) {
  .header__nav-top-content {
    justify-content: space-between;
  }
}
.header__logo {
  margin-right: auto;
}
@media (max-width: 1350px) {
  .header__logo {
    margin: 23px 0 23px 0;
    width: 100%;
    z-index: 3;
  }
}
@media (max-width: 1020px) {
  .header__logo {
    margin-right: auto;
    width: 100%;
  }
}
@media (max-width: 766px) {
  .header__logo {
    order: -2;
    width: 50%;
  }
}
.header__hr {
  width: 100%;
  border: 0.5px solid #E6E8EC;
  margin: 0;
  margin-top: 20px;
}
.header__hr_top {
  display: none;
  width: 100%;
  border: none;
  margin: 0;
}
@media (max-width: 1350px) {
  .header .logo-img {
    width: 112px;
    margin-right: auto;
  }
}
@media (max-width: 766px) {
  .header .logo-img {
    width: 87px;
  }
}
.header__burger-button {
  display: none;
}
@media (max-width: 1020px) {
  .header__burger-button {
    display: inline-flex;
    margin-right: auto;
  }
}
@media (max-width: 766px) {
  .header__burger-button {
    width: 50%;
    margin: 13px 0;
  }
}
.header__burger-icon g rect {
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.header__burger-icon g rect:nth-child(1) {
  transform-origin: -6% 28%;
}
.header__burger-icon g rect:nth-child(2) {
  transform-origin: 0% 73%;
}
.header__burger-button_active > .header__burger-icon g rect:nth-child(3) {
  opacity: 0;
}
.header__burger-button_active > .header__burger-icon g rect:nth-child(1) {
  transform: rotate(45deg);
}
.header__burger-button_active > .header__burger-icon g rect:nth-child(2) {
  transform: rotate(-45deg);
}
.header__nav-list-top {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  max-width: 57%;
  margin-right: 80px;
  width: 100%;
  padding: 0;
  font-family: "Muller", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 20px;
}
@media (max-width: 1350px) {
  .header__nav-list-top {
    max-width: 77%;
    margin-right: 34px;
  }
}
@media (max-width: 1020px) {
  .header__nav-list-top {
    display: none;
  }
}
.header__nav-item {
  overflow-wrap: anywhere;
}
.header__nav-item:not(:last-child) {
  margin-right: 57px;
}
.header__nav-link {
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
}
@media (max-width: 766px) {
  .header__nav-link {
    overflow-wrap: anywhere;
  }
}
.header__nav-link:hover {
  color: #6D31EE;
}
.header__nav-link:focus {
  outline: none;
  color: #fff;
  background-color: #AA9DFA;
}
.header__nav-link:active {
  color: #4F28A5;
  background-color: transparent;
}
.header__search-form {
  position: relative;
  display: inline-block;
  margin-right: 24px;
}
@media (max-width: 1020px) {
  .header__search-form {
    margin-left: 0;
    margin-right: 3.7%;
  }
}
@media (max-width: 766px) {
  .header__search-form {
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    align-items: center;
    width: 50%;
    margin-left: 0;
    margin-right: 0;
  }
}
.header__search-input {
  position: absolute;
  top: 0;
  right: 50px;
  width: 0;
  height: 24px;
  border: 0px solid transparent;
  border-bottom: 1px solid #6D31EE;
  padding-top: 5px;
  padding-bottom: 3px;
  transition-property: width, transform, opacity;
  transition-duration: 0.3s;
  transition-delay: 0.1s;
  transition-timing-function: ease-in-out;
}
.header__search-input::placeholder {
  color: #A1A6B4;
  font-family: "Muller";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
}
.header__search-input_hidden {
  opacity: 0;
  width: 0;
}
.header__search-input_visible {
  opacity: 1;
  width: 278px;
}
@media (max-width: 766px) {
  .header__search-button {
    margin-right: 0;
  }
}
.header__search-button svg path {
  transition: stroke 0.3s ease-in-out;
}
.header__search-button:hover svg path {
  stroke: #AA9DFA;
}
.header__search-button:focus {
  outline: none;
}
.header__search-button:focus svg path {
  stroke: #AA9DFA;
}
.header__search-button:active svg path {
  stroke: #4F28A5;
}
.header__search-button_opened svg path {
  stroke: #6D31EE;
}
.header__button {
  display: inline-flex;
  max-width: 105px;
  width: 100%;
  padding: 10px 31px;
  border: 2px solid #121723;
  border-radius: 77px;
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
}
@media (max-width: 1350px) {
  .header__button {
    margin: 20px 0;
  }
}
@media (max-width: 766px) {
  .header__button {
    z-index: 3;
    order: -1;
    margin: 9px 0 10px;
    max-width: 91px;
    padding: 7.5px 22px;
  }
}
.header__button-text {
  overflow-wrap: anywhere;
  display: inline-block;
  margin: 0;
  font-family: "Muller";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  transition: color 0.3s ease-in-out;
}
.header__button:hover {
  background-color: #121723;
}
.header__button:hover .header__button-text {
  color: #fff;
}
.header__button:focus {
  outline: none;
